.errorPage {
    justify-content: center;
    display: flex;
    text-align: center;
    color: white;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    flex-direction: column;
    background-image: url("404page_bg.jpg");
    background-position: center;
    background-size: cover; /*//для сохранеия пропорций*/
    font-family: Sintony, sans-serif;
    align-items: center;
}

.errorPage::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(125deg, rgba(45, 64, 82, 0.4) 0%, rgba(0, 0, 0, 0.7) 100%);
}

h1 {
    font-family: Sintony, sans-serif;
    font-weight: 500;
    margin: 0;
    line-height: 176px;
    font-size: 160px;
    text-transform: uppercase;
    position: relative;
}

h1::before {
    position: absolute;
    content: "\f4d7";
    font-family: "Ionicons";
    color: #fa6e4f;
    font-size: 52px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

